@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Spartan", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  height: 100%;
  width: 100%;

  background: url(/Frontend-Mentor-Challenges/social-proof-section/static/media/bg-pattern-top-desktop.0e1f3813.svg) left top no-repeat,
    url(/Frontend-Mentor-Challenges/social-proof-section/static/media/bg-pattern-bottom-desktop.1ccf745a.svg) bottom right no-repeat;
}

/* Hide scrollbar */
.app {
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
}
::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}

/* Mobile */
@media (max-width: 550px) {
  .app {
    background: url(/Frontend-Mentor-Challenges/social-proof-section/static/media/bg-pattern-top-mobile.f55aa9cc.svg) left top no-repeat,
      url(/Frontend-Mentor-Challenges/social-proof-section/static/media/bg-pattern-bottom-mobile.02b87d67.svg) bottom right no-repeat;
  }
}

.star {
  display: flex;
  background-color: #f7f2f8;
  width: 450px;
  height: 50px;
  align-items: center;
  border-radius: 5px;
  margin-bottom: 15px;
}

.star:nth-child(3n + 1) {
  margin-left: 0px;
}

.star:nth-child(3n + 2) {
  margin-left: 50px;
}

.star:nth-child(3n + 3) {
  margin-left: 100px;
}

.star__stars {
  display: flex;
  padding-left: 30px;
  padding-right: 30px;
}

.star__stars > img {
  margin-right: 5px;
}

.star__description {
  color: #4e244f;
  font-size: 13px;
  font-weight: 700;
}

/* Tablet */
@media (max-width: 1200px) {
  .star:nth-child(3n + 1) {
    margin-left: 0px;
  }

  .star:nth-child(3n + 2) {
    margin-left: 0px;
  }

  .star:nth-child(3n + 3) {
    margin-left: 0px;
  }
}

/* Mobile */
@media (max-width: 551px) {
  .star {
    width: 350px;
    height: 75px;
    flex-direction: column;
    justify-content: center;
  }

  .star__description {
    color: #4e244f;
    font-size: 13px;
  }

  .star__stars {
    padding-bottom: 10px;
  }
}

.description {
  display: flex;
  padding-top: 15vh;
  padding-bottom: 10vh;
  justify-content: center;

  /* justify-content: space-evenly; */
}

.description__left {
  width: 20vw;
  min-width: 400px;
  padding-right: 100px;
}

.description__left > h1 {
  color: #4e244f;
  font-size: 40px;
}

.description__left > p {
  padding-top: 10px;
  color: hsl(303, 10%, 53%);
  font-size: 13px;
  font-weight: 600;
  line-height: 20px;
}

/* Tablet */
@media (max-width: 1200px) {
  .description {
    flex-direction: column;
    align-items: center;
    padding-top: 10vh;
    padding-bottom: 5vh;
  }

  .description__left {
    width: 20vw;
    min-width: 500px;
    padding-right: 0px;
    padding-bottom: 50px;
  }
}

/* Mobile */
@media (max-width: 551px) {
  .description {
    text-align: center;
  }

  .description__left > h1 {
    font-size: 35px;
  }

  .description__left {
    min-width: 350px;
  }

  .description__left > p {
    text-align: center;
  }
}

.reviewCard {
  background-color: #511f50;
  color: white;
  width: 310px;
  height: 200px;

  padding: 20px;
  margin-right: 30px;

  border-radius: 8px;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.reviewCard__info {
  display: flex;
}

.reviewCard__info,
.reviewCard__description {
  margin-left: 20px;
}

.reviewCard__avatar {
  border-radius: 50%;
  object-fit: contain;
  width: 50px;
}

.reviewCard__personalInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 20px;
}

.reviewCard__name {
  font-size: 12px;
  font-weight: 700;
  margin-bottom: 3px;
  color: hsl(300, 24%, 96%);
}

.reviewCard__buyer {
  font-size: 13px;
  color: hsl(333, 80%, 67%);
}

.reviewCard__description {
  font-size: 12px;
  line-height: 20px;
  color: hsl(300, 24%, 96%);
}

.reviewCard:nth-child(3n + 1) {
  margin-top: 0px;
}

.reviewCard:nth-child(3n + 2) {
  margin-top: 20px;
}

.reviewCard:nth-child(3n + 3) {
  margin-top: 40px;
}

/* Tablet */
@media (max-width: 1200px) {
  .reviewCard:nth-child(3n + 1) {
    margin-top: 0px;
  }

  .reviewCard:nth-child(3n + 2) {
    margin-top: 0px;
  }

  .reviewCard:nth-child(3n + 3) {
    margin-top: 0px;
  }

  .reviewCard {
    margin-right: 0px;
    margin-bottom: 20px;
  }
}

.reviews {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

/* Tablet */
@media (max-width: 1200px) {
  .reviews {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding-bottom: 10vh;
  }
}

